@font-face {
	font-family: 'dojo';
	src: url('./fonts/dojo.ttf?fkcsox') format('truetype'),
		url('./fonts/dojo.woff?fkcsox') format('woff'),
		url('./fonts/dojo.svg?fkcsox#dojo') format('svg');
	font-weight: normal;
	font-style: normal;
}

/* The root class of the Icon */

.root {
}

/* Added to the icon */

.icon {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'dojo' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	-webkit-font-feature-settings: normal;
	        font-feature-settings: normal;
	font-variant: normal;
	text-transform: none;
	font-size: inherit;
	line-height: inherit;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

/* A clock icon */

.clockIcon:before {
	content: '\e94e';
}

/* A down icon */

.downIcon:before {
	content: '\f078';
}

